import { UserProps } from "@/types/common.types";
import { Socket } from "dgram";
import { stat } from "fs";
import { create } from "zustand";


interface GLobalState {
    socket: WebSocket | null,
    isLogged: boolean,
    login: (user: UserProps) => void,
    logout: () => void,
    user: UserProps | null
    createSocket: () => void,
}


const useglobalStore = create<GLobalState>((set,get) => (
{
    socket: null,
    user: null,
    isLogged: false,
    login: (user) => set(() => ({
        isLogged: true,
        user: user
    })),
    logout: () => set(() => ({
        isLogged: false,
        user: null
    })),
    createSocket: () => {
        const current = get().socket;
        if (!current)
        {
            console.log("INIT...")
            const newSocket = new WebSocket("ws://localhost:4000/sockets");
            newSocket.onopen = () =>
            {
                console.log("connecting to backend...");
                set(() => ({socket: newSocket}));
                const handshake = {type: "handshake", content: get().user?.id};
                // if (newSocket.readyState === WebSocket.OPEN)
                newSocket.send(JSON.stringify(handshake));
            }
            newSocket.onclose = () =>
            {
                console.log("closing socket...");
                set(() => ({socket: null}));
            }
            newSocket.onerror = (event) =>
            {
                console.log("error: ", event.target);
            }
        }
    }
}))


export default useglobalStore;
